<script>
export default {
  template: '<div/>',
  created () {
    this.init()
  },
  methods: {
    init () {
      let token = this.$store.getters.getToken
      let newtoken = this.$route.params.token
      if (newtoken) {
        this.$store.commit('setToken', newtoken)
        this.$router.push('/index')
      }
      if (!token && !newtoken) {
        window.location.href = window._config.LOGIN_PAGE
      }
    }
  }
}

</script>



